<template>
  <section id="main-content" class="main-content">
   <div class="left-menu-tab new_tab">   
       <div class="left-menu-tab div-click-event hr_jobpost_a">
             <div class="col-md-12 p-0">
                 <div class="theme-primary partition">
                   <span class="p-name text-white textelipsis">{{ $t('EventExpensesReport') }}</span>
                 </div>
             </div> 
             <div class="pt-2 mb-1 w-100 d-none d-lg-block">
                 <div class="input-group customsearchbox">
                     <input type="text" maxlength="50" v-model="SearchText" @keyup="SearchTab" placeholder="Search By Name" class="form-control border-right-0" autocomplete="off">
                     <div class="input-group-append"><span class="input-group-text bg-transparent bg-white border-left-0"><i class="fa fa-search"></i></span></div>
                 </div>
             </div>      
             <ul class="nav nav-pills flex-column left-tab mt-2 responivecustomtab" id="myTab" role="tablist">
               <li class="nav-item" v-if="UserType=='CA' || UserType=='NU' || UserType=='PM'">
                   <a
                     class="nav-link left-menu"
                     :class="{ 'active': activeKey == 1}"
                     data-bs-target="#tab-overlimlit-content"
                     href="javascript:void(0)"
                     data-bs-toggle="tab"
                     role="tab"
                     id="tab-ExpenseOverLimit"
                     v-on:click="loadData('ExpenseOverLimit');"
                   >
                     <em class="fa fa-print clsexpense" aria-hidden="true" aria-selected="true"></em>
                     {{ $t('ExpenseOverLimit') }}
                   </a>
               </li>
               <li class="nav-item" v-if="UserType=='CA'">
                   <a
                     class="nav-link left-menu"
                     :class="{ 'active': activeKey == 2 }"
                     data-bs-target="#tab-expensereport-content"
                     href="javascript:void(0)"
                     id="tab-expensereport"
                     data-bs-toggle="tab"
                     role="tab"
                     v-on:click="loadData('expensereport')">
                     <em class="fa fa-file-text-o" aria-hidden="true" aria-selected="false"></em>
                     {{ $t('ExpenseReport') }}
                   </a>
               </li>
               <li class="nav-item" v-if="UserType=='CA' ||  UserType=='PM'">
                     <a
                       class="nav-link left-menu"
                       :class="{ 'active': activeKey == 3 }"
                       data-bs-target="#tab-Reimbursements-content"
                       href="javascript:void(0)"
                       id="tab-attachment"
                       data-bs-toggle="tab"
                       role="tab"
                       v-on:click="loadData('Reimbursements')">
                       <em class="fa fa-file-pdf-o" aria-hidden="true" aria-selected="false"></em>
                       {{ $t('Reimbursements') }}
                     </a>
               </li>
             </ul>
       </div>
   </div>
  <div class="right-content"> 
   <div class="col-md-12 p-0">
     <div class="theme-primary partition p-actions-expand" style="overflow: visible;">
           <span class="p-name text-white" id="spnreporttext">{{ this.headerText }}</span>                
     </div> 
   </div>
     <div class="col-md-12 p-0">
         <div class="tab-content" id="myTabContent">  
           <div class="expand-list">
                   <a href="javascript:;" class="expand-list-btn justify-content-center align-items-center">
                       <i class="icon-solid arrow-solid-left text-white" style="font-size:9pt"></i>
                   </a>
               </div> 
               <div class="tab-pane pb-2" :class="{ 'active show': activeKey == 1 }" role="tabpanel" id="tab-overlimlit-content" aria-labelledby="tab-overlimlit-content">
                 <div id="listing">                     
                   <div class="col-md-12 p-0">
                   <Form  ref="Expenseform">
                       <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit"></dynamic-form>
                       
                   </Form>
                 </div>
               </div>
             </div>
             <div v-if="expensereportTab" class="tab-pane pb-2" :class="{ 'active show': activeKey == 2 }" role="tabpanel" id="tab-expensereport-content" aria-labelledby="tab-expensereport-content">
               <div class="listing">
                 <Form  ref="Expenseform">
                   <dynamic-form lang="en"  :buttons="buttons" :schema="FormSchema1" v-on:OnSubmit="onSubmit1"></dynamic-form>                    
                 </Form>
               </div>
             </div>
             <div v-if="ReimbursementsTab" class="tab-pane pb-2" :class="{ 'active show': activeKey == 3 }" role="tabpanel" id="tab-Reimbursements-content" aria-labelledby="tab-Reimbursements-content">
                 <div class="listing">
                   <Form  ref="Expenseform">
                       <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema2" v-on:OnSubmit="onSubmit2"></dynamic-form>                        
                   </Form>
                 </div>
             </div>
             <div class="col-lg-12 float-left p-0 ">
                 <div class="col-md-12  placeholder border mt-0 py-3 w-100 float-left border-top-0">
                     <ReportViewer v-if="parameter.length>0" :parameter="parameter" :reportPath="reportPath"></ReportViewer>                 
                 </div>
             </div>
           </div>
       </div>
 </div>
</section>
</template>
<script>
import DataService from "@/services/DataService";
import ReportViewer from './ReportViewer.vue'
export default {
 components:{
     ReportViewer
 },
 data: function () {
     return {
       UserType:null,
       SearchText:'',
       activeKey:1,
       headerText:'Expense Over Limit',
       FormSchema: [
               {
                   layoutType: "triple",
                   Data: [
                   {
                       astype: "MultiSelectField",
                       label: "Project",
                       name: "Project",
                       value: "",
                       mode:"tag",
                       placeholder: "Select",
                       config:{
                         options: [],
                       },
                       validationRules: {"required": false},
                   },
                   {
                     astype: "MultiSelectField",
                     label: "Category",
                     name: "Category",
                     value: "",
                     mode:"tag",
                     placeholder: "Select",
                     config:{
                       options: []
                     },
                     validationRules: {"required": false},
                   },
                   {
                     astype: "CheckBoxField",
                     label: "Over Limit",
                     name: "OverLimit",
                     value: "",
                     placeholder: "",
                     validationRules: {"required": false},
                     config:{
                       options: [{value: "1"}]
                     },
                   },
                   {
                        astype: "DateField",
                        label: 'Date From (M/D/YYYY)',
                        name: "ExpenseDateFrom",
                        value: '',
                        placeholder: "M/D/YYYY",
                        validationRules: {"required": false},
                        config: {
                            format: "MM/DD/YYYY"
                        }
                    },
                    {
                        astype: "DateField",
                        label: 'Date To (M/D/YYYY)',
                        name: "ExpenseDateTo",
                        value: '',
                        placeholder: "M/D/YYYY",
                        validationRules: {"required": false},
                        config: {
                            format: "MM/DD/YYYY"
                        }
                    }
                   ],
               }
       ],
       FormSchema1: [
               {
                   layoutType: "triple",
                   Data: [
                   {
                       astype: "MultiSelectField",
                       label: "Project",
                       name: "Project",
                       value: "",
                       placeholder: "Select",
                       config:{
                         options: [],
                         onChange: this.onChangeDropdown
                       },
                       validationRules: {"required": false},
                   },
                   {
                     astype: "MultiSelectField",
                     label: "Category",
                     name: "Category",
                     value: [],
                     placeholder: "Select",
                     config:{
                       options: []
                     },
                     validationRules: {"required": false},
                   },
                   {
                     astype: "MultiSelectField",
                     label: "Trip",
                     name: "Trip",
                     value: [],
                     placeholder: "Select",
                     config:{
                       options: []
                     },
                     validationRules: {"required": false},
                   },
                   {
                     astype: "MultiSelectField",
                     label: "User",
                     name: "User",
                     value: [],
                     placeholder: "Select",
                     config:{
                       options: []
                     },
                     validationRules: {"required": false},
                   },
                   {
                     astype: "MultiSelectField",
                     label: "Approver",
                     name: "Approver",
                     value: [],
                     placeholder: "Select",
                     config:{
                       options: []
                     },
                     validationRules: {"required": false},
                   },
                   {
                     astype: "MultiSelectField",
                     label: "Status",
                     name: "Status",
                     value: [],
                     placeholder: "Select",
                     config:{
                       options: []
                     },
                     validationRules: {"required": false},
                   },
                   {
                       astype: "TextField",
                       label: this.$t('ExpenseTitletext'),
                       name: "ExpenseTitle",
                       value: "",
                       placeholder: "",
                       validationRules: { "required": false, max: 250 },
                   },
                   {
                        astype: "DateField",
                        label: 'Date From (M/D/YYYY)',
                        name: "ExpenseDateFrom",
                        value: '',
                        placeholder: "M/D/YYYY",
                        validationRules: {"required": false},
                        config: {
                            format: "MM/DD/YYYY"
                        }
                    },
                    {
                        astype: "DateField",
                        label: 'Date To (M/D/YYYY)',
                        name: "ExpenseDateTo",
                        value: '',
                        placeholder: "M/D/YYYY",
                        validationRules: {"required": false},
                        config: {
                            format: "MM/DD/YYYY"
                        }
                    },
                    {
                     astype: "RadioField",
                     label: "Group By",
                     name: "GroupBy",
                     value: "Project",
                     config:{
                       options: [{value: "Project", name: "Project"},{value: "Category", name: "Category"},{value: "Trip", name: "Trip"}]
                     },
                     validationRules: {"required": false}
                   }
                   ],
               }
       ],
       FormSchema2: [
               {
                   layoutType: "triple",
                   Data: [
                   {
                       astype: "MultiSelectField",
                       label: "Project",
                       name: "Project",
                       value: [],

                       placeholder: "Select",
                       config:{
                         options: []
                       },
                       validationRules: {"required": false},
                   },
                   {
                     astype: "MultiSelectField",
                     label: "The Approver",
                     name: "TheApprover",
                     value: [],
                     placeholder: "Select",
                     config:{
                       options: []
                     },
                     validationRules: {"required": false},
                   },
                   {
                     astype: "MultiSelectField",
                     label: "User",
                     name: "User",
                     value: [],
                     placeholder: "Select",
                     config:{
                       options: []
                     },
                     validationRules: {"required": false},
                   },
                   {
                     astype: "RadioField",
                     label: "Group By",
                     name: "GroupBy",
                     value: "Company",
                     config:{
                       options: [{value: "Company", name: "Company"},{value: "Client", name: "Client"}]
                     },
                     validationRules: {"required": false}
                   },
                   {
                        astype: "DateField",
                        label: 'Date From (M/D/YYYY)',
                        name: "ExpenseDateFrom",
                        value: '',
                        placeholder: "M/D/YYYY",
                        validationRules: {"required": false},
                        config: {
                            format: "MM/DD/YYYY"
                        }
                    },
                    {
                        astype: "DateField",
                        label: 'Date To (M/D/YYYY)',
                        name: "ExpenseDateTo",
                        value: '',
                        placeholder: "M/D/YYYY",
                        validationRules: {"required": false},
                        config: {
                            format: "MM/DD/YYYY"
                        }
                    }
                   ],
               }
       ],
       buttons: [
               {
                   type: "submit",
                   class: "btn btn-success",
                   text: "<i class='fa fa-search pr-2'></i>Search",
               },
               {
                   type: "button",
                   class: "btn btn-danger",
                   text: "<i class='fa fa-close pr-2'></i>Clear All",
                   onClick:this.onClear
               }
       ],
       reportPath:null,
       parameter:[],
       ExpenseOverLimittab:true,
       expensereportTab:false,
       ReimbursementsTab:false,
       componentKey:0
     }
 },
 created:function(){     
       this.GetProjectByCompanyIdAndUserId();
       this.GetAllCategoriesByCompanyId();
       this.ExpenseGetApproverListing();
       this.GetTripNameDDL();
       this.GetStatusList("Expense", "en");
       this.UserType=this.GetUserInfo.UserType;
       this.GetUsersByProjectIds();
 },
 methods:{
   SearchTab:function(event){
       var val = this.SearchText;
       $("ul#myTab").find("li").each(function () {
           val = val.toLowerCase();
           var text = $(this).find("a").text().toLowerCase();
           if (text.includes(val)) {
               $(this).closest("li").show();
           }
           else {
               $(this).closest("li").hide();
           }
       });
   },
   loadData:function(tabName){
           var vm=this;
           if(tabName=='expensereport'){              
               vm.activeKey=2;
               vm.ExpenseOverLimittab=false;
               vm.expensereportTab=true;
               vm.Reimbursements=false;
               vm.headerText='Expense Report';
               vm.parameter=[]
               vm.componentKey=1;
           }
           else if(tabName=='Reimbursements'){
               vm.activeKey=3;
               vm.ExpenseOverLimittab=false;
               vm.expensereportTab=false;
               vm.ReimbursementsTab=true;
               vm.headerText='Reimbursements';
               vm.parameter=[];
               vm.componentKey=2;
           }
           else{              
               vm.activeKey=1;
               vm.ExpenseOverLimittab=true;
               vm.expensereportTab=false;
               vm.ReimbursementsTab=false;
               vm.headerText='Expense Over limit';
               vm.parameter=[];
               vm.componentKey=0;
           }
   },    
   onClear:function(){
       var vm=this;
       if(vm.componentKey==1){
         (vm.FormSchema1)[0].Data[0].value=[];
         (vm.FormSchema1)[0].Data[1].value=[];
         (vm.FormSchema1)[0].Data[2].value=[];
         (vm.FormSchema1)[0].Data[3].value=[];
         (vm.FormSchema1)[0].Data[4].value=[];
         (vm.FormSchema1)[0].Data[5].value=[];
         (vm.FormSchema1)[0].Data[6].value='';
         (vm.FormSchema1)[0].Data[7].value='';
         (vm.FormSchema1)[0].Data[8].value='';
         (vm.FormSchema1)[0].Data[9].value='';
         vm.parameter=[];
       }
       else if(vm.componentKey==2){
         (vm.FormSchema2)[0].Data[0].value=[];
         (vm.FormSchema2)[0].Data[1].value=[];
         (vm.FormSchema2)[0].Data[2].value=[];        
         (vm.FormSchema2)[0].Data[3].value='';
         (vm.FormSchema2)[0].Data[4].value='';
         (vm.FormSchema2)[0].Data[5].value='';
         vm.parameter=[];
       }
       else{          
         (vm.FormSchema)[0].Data[0].value=[];
         (vm.FormSchema)[0].Data[1].value=[];
         (vm.FormSchema)[0].Data[2].value='';
         (vm.FormSchema)[0].Data[3].value='';
         (vm.FormSchema)[0].Data[4].value='';
         vm.parameter=[];
       }        
   },
   GetProjectByCompanyIdAndUserId:function() {
         var vm = this;
         DataService.GetProjectByCompanyIdAndUserId().then(function (response) {
           response.data.forEach((item, index) => {
                   (vm.FormSchema)[0].Data[0].config.options.push({
                       value: item.projectId,
                       name: item.projectName
                   });
                   (vm.FormSchema2)[0].Data[0].config.options.push({
                       value: item.projectId,
                       name: item.projectName
                   });
                   (vm.FormSchema1)[0].Data[0].config.options.push({
                       value: item.projectId,
                       name: item.projectName
                   });
               })
         });
   },
   GetAllCategoriesByCompanyId:function() {
           var vm = this;
           DataService.GetAllCategoriesByCompanyId().then(function (response) {
               response.data.forEach((item, index) => {
                   (vm.FormSchema)[0].Data[1].config.options.push({
                       value: item.expensE_CATEGORY_ID,
                       name: item.categorY_NAME
                   });
                   (vm.FormSchema1)[0].Data[1].config.options.push({
                       value: item.expensE_CATEGORY_ID,
                       name: item.categorY_NAME
                   });
           });
       });
   },
   ExpenseGetApproverListing:function(){
           let params = {};
           var vm=this;
           params["IsEncrptedId"] = '';
           params["moduleName"] = 'Expenses';
           DataService.ExpenseGetUserListing(params).then(function (response) {
               response.data.forEach((item, index) => {
                   (vm.FormSchema2)[0].Data[1].config.options.push({
                       value: item.value,
                       name: item.name
                   });
                   (vm.FormSchema2)[0].Data[2].config.options.push({
                       value: item.value,
                       name: item.name
                   });
                   (vm.FormSchema1)[0].Data[4].config.options.push({
                       value: item.value,
                       name: item.name
                   });                    
               });
           });
   },
   GetTripNameDDL: function () {
           var vm = this;
           DataService.GettTripListingddl().then(function (response) {
                 response.data.forEach((item, index) => {
                   (vm.FormSchema1)[0].Data[2].config.options.push({
                       value: item.TRIP_ID,
                       name: item.TRIP_NAME
                   });
               });
           });
   },
   GetStatusList: function (StatusType, lang) {
           var vm = this;
           var url = "langCode=" + lang + "&statusType=" + StatusType + "&hasglobal=true";
           let obj={};
           DataService.GetStatusList(url).then(function (response) {
               let DObj={};
               response.data.forEach((item) => {
                 if(item.STATUS=='ApprovalPending'){
                    DObj={
                     value:item.STATUS_ID,
                     name: `Approval Pending`
                   }
                 } 
                 else if(item.STATUS=='SentForCorrection'){
                   DObj={
                     value:item.STATUS_ID,
                     name: `Send For Correction`
                   }
                 }
                 else{
                     DObj={
                       value: item.STATUS_ID,
                       name: item.STATUS
                     }
                 }           
                 (vm.FormSchema1)[0].Data[5].config.options.push(DObj);
               });               
           });
   },
   onSubmit:function(e) {
          var vm=this;
          var projectIds='';
          var categoryIds='';
          e.Project.forEach((item) => {
              projectIds+=item.value+','
          });
          e.Category.forEach((item) => {
              categoryIds+=item.value+','
          });
           var reportModel={
               reportName:'expenseoverlimit',
               moduleName:'Expense',
               projectIds:projectIds,
               categoryIds:categoryIds,
               dateFrom:e.ExpenseDateFrom,
               dateTo:e.ExpenseDateTo,
               expenseOverLimit:e.OverLimit=='1'?1:0
           }  
           var objectPostString = JSON.stringify(reportModel);     
           DataService.ReportMain(objectPostString).then(response => {                                                          
               vm.reportPath=response.data.reportpath;
               vm.parameter=response.data.parameters;
           });
   },
   onSubmit1:function(e) {      
       var vm=this;
       var projectIds='';
       var categoryIds='';
       var ApproverIds='';
       var Tripids='';
       var UserIds='';
       var StatusIds='';
       e.Project.forEach((item) => {
           projectIds+=item.value+','
       });
       e.Category.forEach((item) => {
           categoryIds+=item.value+','
       });
       e.Approver.forEach((item) => {
          ApproverIds+=item.value+','
       });
       e.Trip.forEach((item) => {
          Tripids+=item.value+','
       });
       e.User.forEach((item) => {
          UserIds+=item.value+','
       });
       e.Status.forEach((item) => {
          StatusIds+=item.value+','
       });
       var reportModel={
           reportName:'expensereport',
           moduleName:'Expense',
           projectIds:projectIds,
           categoryIds:categoryIds,
           dateFrom:e.ExpenseDateFrom,
           dateTo:e.ExpenseDateTo,
           approverIds:ApproverIds,
           tripIds:Tripids,
           userIds:UserIds,
           StatusIds:StatusIds,  
           expTitle:e.ExpenseTitle,
           groupByVal:(vm.FormSchema1)[0].Data[9].value,    
       }
       var objectPostString = JSON.stringify(reportModel);     
       DataService.ReportMain(objectPostString).then(response => { 
                vm.reportPath=response.data.reportpath;
                vm.parameter=response.data.parameters;
       });
   }, 
   onSubmit2:function(e) {
       var vm=this;
       var projectIds='';
       var ApproverIds='';
       var UserIds='';
       e.Project.forEach((item) => {
           projectIds+=item.value+','
       });
       e.TheApprover.forEach((item) => {
          ApproverIds+=item.value+','
       });
       e.User.forEach((item) => {
          UserIds+=item.value+','
       });
       var reportModel={
           reportName:'reimbursementreport',
           moduleName:'Expense',
           projectIds:projectIds,            
           dateFrom:e.ExpenseDateFrom,
           dateTo:e.ExpenseDateTo,
           approverIds:ApproverIds,           
           userIds:UserIds,
           groupByVal:(vm.FormSchema2)[0].Data[3].value,                    
       }  
       var objectPostString = JSON.stringify(reportModel);     
       DataService.ReportMain(objectPostString).then(response => {                                                      
           vm.reportPath=response.data.reportpath;
           vm.parameter=response.data.parameters;
       });
   },  
   GetUsersByProjectIds:function(e){     
     var vm = this;    
     var params=`projectIds=&moduleName=Project`
     DataService.GetUsersByProjectIds(params).then(function (response) {      
       response.data.forEach((item)=>{
         (vm.FormSchema1)[0].Data[3].config.options.push({
               value: `${item.USER_ID}`,
               name: item.USER_NAME
           });
       })
     });
   }  
 }
}
</script>